@font-face {
  font-family: 'Circe';
  src: url('fonts/Circe/subset-Circe-Bold.eot');
  src: url('fonts/Circe/subset-Circe-Bold.eot?#iefix') format('embedded-opentype'),
  url('fonts/Circe/subset-Circe-Bold.woff2') format('woff2'),
  url('fonts/Circe/subset-Circe-Bold.woff') format('woff'),
  url('fonts/Circe/subset-Circe-Bold.ttf') format('truetype'),
  url('fonts/Circe/subset-Circe-Bold.svg#Circe-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
}

